 economía, rápidamente se comprende que toda sociedad, independientemente de su nivel de desarrollo o sistema político, debe responder a tres preguntas esenciales: ¿qué producir?, ¿cómo producir? y ¿para quién producir? Estas interrogantes surgen como consecuencia de un problema central: la escasez de recursos frente a necesidades humanas ilimitadas. Dado que no es posible producir todo para todos, se deben hacer elecciones y priorizar el uso de los recursos disponibles (Burneo, K. 2016).")])]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Burneo, K. en su libro “Principios de economía” explica lo que significa ¿qué?, ¿cómo? y ¿para quién producir? de la siguiente manera:")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/12.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"bg-color-5 h-100 p-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema1/13.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("Estas tres preguntas fundamentales están estrechamente relacionadas con las leyes de la oferta y la demanda, pilares de análisis económico. La demanda expresa el comportamiento de los consumidores ante distintos precios, y la oferta, el comportamiento de los productores. La interacción entre ambas determina los precios de mercado que, a su vez, guían las decisiones sobre qué, cómo y para quién producir. De este modo, los precios funcionan como señales que ayudan en la asignación de los recursos escasos, de forma más eficiente en economías de mercado.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c